import React from 'react'
import {NavLink,} from "react-router-dom"
import  "../css/navbar.css"
import { Button } from '@chakra-ui/react'
import rglogo from "../assets/rglogo.jpg"
import resume from "../assets/R_Giri_Reddy_Resume.pdf"
import { Link } from 'react-scroll'

const Navbar = () => {

  return (
    <div className='navwrapper' name='Navbar'>
    <div className="n-left">
    <img src={rglogo} alt="" style={{width:"70px",borderRadius:"50%"}}/>
      <div className="n-name">
        R Giri Reddy
      </div>
      <span></span>
    </div>
    <div className="n-right">
    <div className="n-list">
    <ul>
    <Link activeClass='active' to='Navbar'  spy={true} smooth={true} ><li>Home</li></Link> 
     <li>About me</li>
     <li>Skills</li> 
     <li>Projects</li>
      <li><a target="_blank" rel="noopener" href={resume}  download >Resume</a></li>
     </ul>
    </div>

    </div>
    
     <button className='button n-btn'>Contact me</button>
    
    </div>
  )
}

export default Navbar