import React from 'react'
import htmlimg from "../Images/htmlimg.png"
import cssimg from "../Images/cssimg.png"
import reactimg from "../Images/reactimg.png"
import reduximg from "../Images/reduximg.png"
import jsimg from "../Images/jsimg.png"
import chakra from "../Images/chakra.png"
import git from "../Images/gitimg.png"
import net from "../Images/netlify.png"
import node from "../Images/node.png"
import npm from "../Images/npm.png"
import mongo from "../Images/mongo.png"
import vercel from "../Images/vercel.png"
import "../css/skills.css"

const Skills = () => {
  return (
    <div>
     <h1 className='s-head' >Technical Skills</h1>
    <div className='s-main'>
   
    <img src={htmlimg} alt="img" />
    <img src={cssimg} alt="img" />
    <img src={reactimg} alt="img" />
    <img src={reduximg} alt="img" />
    <img src={jsimg} alt="img" />
    <img src={chakra} alt="img" />
    <img src={git} alt="img" />
    <img src={npm} alt="img" />
    <img src={net} alt="img" />
    <img src={node} alt="img" />
    <img src={mongo} alt="img" />
    <img src={vercel} alt="img" />
      
    </div>
    </div>
  )
}

export default Skills